<template>

    <div class="addpastjob">
        <MainPageStructure title="Munkatapasztalat hozzadsa">
            <form v-on:submit.prevent @submit="saveJob">
                <label for="company">Munkahely/cg neve:</label><br>
                <input v-model="company" name="company" type="text" placeholder="pl.: Trr Istvn Gazdasgi Szakgimnzium" required><br>
                <label for="date">Mettl, meddig dolgoztl itt?:</label><br>
                <input v-model="date" name="date" type="text" placeholder="pl.: 2016-2020" required><br>
                <label for="scope">Betlttt munkakr:</label><br>
                <input v-model="scope" name="scope" type="text" placeholder="pl.: Rendszergazda" required>
                <br><br>
                <div class="buttonwrapper"><button type="submit">Hozzads</button></div>
            </form>
        </MainPageStructure>    
    </div>

</template>

<script>

import axios from 'axios';
import MainPageStructure from '@/components/MainPageStructure.vue';

export default {

    components: {
        MainPageStructure
    },

    data() {
        return {
            company: '',
            date: '',
            scope: ''
        }
    },

    methods: {
        saveJob() {
            axios.post('http://localhost:8080/api/info/add/job', {
                employeeId: sessionStorage.getItem('id'), 
                job: this.company, 
                date: this.date, 
                scope: this.scope
            })
            .then(response => {
                if(response.status == 201) {
                    this.$router.push('/profile');
                    this.$forceUpdate();
                }
            });
        }
    }
}

</script>
